// services/validation/validationService.js

/**
 * Valida os dados do template antes de enviar para a API
 * @param {string} templateName - Nome do template
 * @param {string} bodyText - Texto do corpo da mensagem
 * @param {string} authKey - Chave de autorizao da API
 * @param {Array} cards - Lista de cards do carrossel
 * @throws {Error} Lana erro se alguma validao falhar
 */
export const validateTemplate = (templateName, bodyText, authKey, cards) => {
  // Validao do nome do template
  if (!templateName) {
    throw new Error('Nome do template  obrigatrio');
  }
  
  if (templateName.length < 3) {
    throw new Error('Nome do template deve ter pelo menos 3 caracteres');
  }
  
  // Validao do texto do corpo
  if (!bodyText) {
    throw new Error('Texto do corpo da mensagem  obrigatrio');
  }
  
  if (bodyText.length > 1024) {
    throw new Error(`Texto do corpo excede o limite de 1024 caracteres. Atual: ${bodyText.length}`);
  }
  
  // Validao da chave de autorizao
  if (!authKey) {
    throw new Error('A chave de autorizao (Router Key)  obrigatria');
  }
  
  // Validao do nmero de cards
  if (!cards || cards.length < 2) {
    throw new Error(' necessrio pelo menos 2 cards para criar um carrossel');
  }
  
  if (cards.length > 10) {
    throw new Error('O carrossel pode ter no mximo 10 cards');
  }
  
  // Validao detalhada de cada card
  cards.forEach((card, i) => {
    // Validar URL do arquivo
    if (!card.fileUrl) {
      throw new Error(`URL do arquivo  obrigatria para o card ${i + 1}`);
    }
    
    // Validar texto do card
    if (!card.bodyText) {
      throw new Error(`Texto do corpo  obrigatrio para o card ${i + 1}`);
    }
    
    if (card.bodyText.length > 160) {
      throw new Error(`Texto do card ${i + 1} excede o limite de 160 caracteres. Atual: ${card.bodyText.length}`);
    }
    
    // Validar file handle
    if (!card.fileHandle) {
      throw new Error(`File Handle no encontrado para o card ${i + 1}. Voc deve concluir o upload na etapa anterior.`);
    }
    
    // Validar botes
    if (!card.buttons || card.buttons.length === 0) {
      throw new Error(`Adicione pelo menos um boto para o card ${i + 1}`);
    }
    
    if (card.buttons.length > 2) {
      throw new Error(`O card ${i + 1} pode ter no mximo 2 botes`);
    }
    
    // Validar cada boto do card
    card.buttons.forEach((button, j) => {
      if (!button.text) {
        throw new Error(`Texto do boto ${j+1} no card ${i+1}  obrigatrio`);
      }
      
      if (button.text.length > 25) {
        throw new Error(`Texto do boto ${j+1} no card ${i+1} excede o limite de 25 caracteres. Atual: ${button.text.length}`);
      }
      
      if (button.type === 'URL' && !button.url) {
        throw new Error(`URL do boto ${j+1} no card ${i+1}  obrigatrio`);
      }
      
      if (button.type === 'PHONE_NUMBER' && !button.phoneNumber) {
        throw new Error(`Nmero de telefone do boto ${j+1} no card ${i+1}  obrigatrio`);
      }
    });
  });
};

/**
 * Valida e formata um nmero de telefone
 * @param {string} phoneNumber - Nmero de telefone a ser validado
 * @returns {string} Nmero de telefone formatado
 * @throws {Error} Lana erro se o nmero for invlido
 */
export const validatePhoneNumber = (phoneNumber) => {
  // Remove todos os caracteres no numricos
  const cleaned = phoneNumber.replace(/\D/g, '');
  
  // Verifica se o nmero tem um formato vlido
  if (cleaned.length < 10) {
    throw new Error('Nmero de telefone invlido. Use formato com DDI+DDD+Nmero (ex: 5521999999999)');
  }
  
  // Valida cdigo do pas
  const countryCode = cleaned.substring(0, 2);
  if (countryCode !== '55') {
    console.warn(`Cdigo de pas no  do Brasil: ${countryCode}`);
    // No lanamos erro, apenas avisamos, pois pode ser um nmero internacional vlido
  }
  
  return cleaned;
};

/**
 * Valida arquivos antes do upload
 * @param {File} file - Arquivo a ser validado
 * @throws {Error} Lana erro se o arquivo for invlido
 */
export const validateFile = (file) => {
  const allowedTypes = [
    'image/jpeg', 'image/png', 'image/gif', 
    'image/webp', 'video/mp4', 'video/webm'
  ];
  const maxFileSize = 50 * 1024 * 1024; // 50MB

  if (!file) {
    throw new Error('Nenhum arquivo foi fornecido');
  }

  if (!allowedTypes.includes(file.type)) {
    throw new Error(`Tipo de arquivo no suportado: ${file.type}. Tipos permitidos: JPEG, PNG, GIF, WebP, MP4, WebM`);
  }

  if (file.size > maxFileSize) {
    throw new Error(`Tamanho do arquivo excede o limite de 50MB. Tamanho atual: ${(file.size / 1024 / 1024).toFixed(2)}MB`);
  }
};

/**
 * Valida URLs antes do upload
 * @param {string} url - URL a ser validada
 * @returns {boolean} Verdadeiro se a URL for vlida
 */
export const isValidUrl = (url) => {
  try {
    new URL(url);
    return true;
  } catch (e) {
    return false;
  }
};

/**
 * Verifica se os cards tm URLs vlidas para upload
 * @param {Array} cards - Array de cards a serem validados
 * @throws {Error} Lana erro se alguma URL for invlida
 */
export const validateFileUrls = (cards) => {
  cards.forEach((card, index) => {
    if (!card.fileUrl) {
      throw new Error(`URL do arquivo no informada para o card ${index + 1}`);
    }
    
    if (!isValidUrl(card.fileUrl)) {
      throw new Error(`URL invlida para o card ${index + 1}: ${card.fileUrl}`);
    }
  });
};

