// services/storage/azureBlobService.js

import { BlobServiceClient } from "@azure/storage-blob";
import { validateFile } from "../validation/validationService";

export class AzureBlobService {
  constructor(accountName, sasToken, containerName = 'uploads') {
    // Validar configuraes com mensagens detalhadas
    if (!accountName) {
      throw new Error('Nome da conta de armazenamento Azure no foi fornecido');
    }

    if (!sasToken) {
      throw new Error('Token SAS para Azure Blob Storage no foi fornecido');
    }

    // URL do servio de blob
    const blobServiceUri = `https://${accountName}.blob.core.windows.net`;

    // Cliente do servio de Blob
    this.blobServiceClient = new BlobServiceClient(
      `${blobServiceUri}?${sasToken}`
    );

    // Cliente do container
    this.containerClient = this.blobServiceClient.getContainerClient(containerName);
  }

  // Mtodo de upload com tratativa de erros detalhada
  async uploadFile(file) {
    try {
      // Validar arquivo
      validateFile(file);

      // Nome nico para o arquivo
      const blobName = `${Date.now()}-${file.name.replace(/\s+/g, '-')}`;
      
      // Cliente do blob especfico
      const blockBlobClient = this.containerClient.getBlockBlobClient(blobName);

      // Opes de upload
      const uploadOptions = {
        blobHTTPHeaders: {
          blobContentType: file.type, // Mantm o tipo MIME original
        }
      };

      try {
        // Realizar upload
        await blockBlobClient.uploadData(file, uploadOptions);

        // Retornar informaes do arquivo
        return {
          url: blockBlobClient.url,
          type: file.type.startsWith('image/') ? 'image' : 'video',
          name: blobName
        };
      } catch (uploadError) {
        // Tratativa especfica para erros de upload
        console.error('Detalhes do erro de upload:', uploadError);

        // Verificar erros de CORS
        if (uploadError.message.includes('CORS')) {
          throw new Error('Erro de CORS. Verifique as configuraes do Azure Blob Storage.');
        }

        // Verificar erros de conexo
        if (uploadError.message.includes('fetch')) {
          throw new Error('Falha na conexo com o Azure Blob Storage. Verifique sua conexo de internet.');
        }

        // Verificar erros de autenticao
        if (uploadError.statusCode === 403 || uploadError.statusCode === 401) {
          throw new Error('Falha na autenticao. Verifique seu token SAS.');
        }

        // Erro genrico de upload
        throw new Error(`Falha no upload do arquivo: ${uploadError.message}`);
      }
    } catch (validationError) {
      // Tratativa de erros de validao
      console.error('Erro de validao:', validationError);
      throw validationError;
    }
  }
}

// Funo de criao do servio com log de erros
export const createAzureBlobService = () => {
  try {
    const accountName = process.env.REACT_APP_AZURE_STORAGE_ACCOUNT_NAME;
    const sasToken = process.env.REACT_APP_AZURE_SAS_TOKEN;
    const containerName = process.env.REACT_APP_AZURE_CONTAINER_NAME || 'uploads';

    // Log de configuraes (cuidado para no logar tokens sensveis)
    // console.log('Configuraes do Azure Blob Storage:', {
    //   accountName,
    //   containerName,
    //   sasTokenPresent: !!sasToken
    // });

    return new AzureBlobService(accountName, sasToken, containerName);
  } catch (error) {
    console.error('Erro ao criar servio de Blob Storage:', error);
    throw error;
  }
};